<template>
	<view>
		<!-- 头部 -->
		<div class="head df fdc">
			<!-- 当前账户：模拟账户 -->
			<div class="account df jcsb aic">
				<div class="left df aic">
					<image class="user-img mr8" src="../../static/fonts/user.png" mode=""></image>
					<p class="fz18 fwb fff">当前账户：模拟账户</p>
				</div>
				<div class="right df aic">
					<image class="user-img mr32" src="../../static/fonts/Headphone1.png" mode=""></image>
					<image class="user-img" src="../../static/fonts/SMS1.png" mode=""></image>
				</div>
			</div>
			<!-- 账户金额 -->
			<div class="fund df aic jcc fff fz40 fwb">$229,375.25</div>
			<p class="df aic jcc fff fz14 mt16">账户可用资金</p>
		</div>
		<!-- 股票、原油、指数 -->
		<div class="container df">
			<div class="item df fdc jcsb">
				<p class="fz14 title">股票</p>
				<p class="number fz14 sign">+3.57%</p>
				<!-- 图表的容器 -->
				<div class="myCharts"></div>
			</div>
		</div>
	</view>
</template>

<script>
	import * as echarts from 'echarts';
	import {
		render
	} from "vue";
	export default {
		data() {
			return {};
		},
		methods: {
			// 定义一个渲染函数，在mounted阶段执行。
			render() {
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.querySelector('.myCharts'));
				let options = {
					tooltip: {},
					grid: {
						left: '0', //距左边边框的距离
						right: '0', //距右边边框的距离
						bottom: '0', //距下面边框的距离
						top: '0', //距上面边框的距离
						containLabel: true,
					},
					xAxis: {
						data: [],
						axisLine: {
							show: false, //隐藏y轴
						},
					},
					yAxis: {
						axisTick: {
							show: false, //刻度线
						},
						axisLine: {
							show: false, //隐藏y轴
						},
						axisLabel: {
							show: false, //隐藏刻度值
						},
						splitLine: {
							show: false,
							lineStyle: {
								type: 'dashed',
							},
						},
					},
					series: [{
						// name: '销量',
						type: 'line',
						data: [10, 3, 12, 0, 15, 13, 18],
						showSymbol: false, //隐藏线形上面的圆点
						lineStyle: {
							color: '#12d18e',
						},
						// 颜色渐变
						areaStyle: {
							normal: {
								color: new echarts.graphic.LinearGradient(
									0, 0, 0, 1,
									[{
											offset: 0,
											color: '#12d18e'
										},
										{
											offset: 0.5,
											color: '#12d18e',
										},
										{
											offset: 1,
											color: '#ddd'
										}
									]
								)
							}
						},
					}]
				}
				// 绘制图表
				myChart.setOption(options);
			},
		},
		// 页面挂在之后执行渲染函数。
		mounted() {
			this.render()
		}
	}
</script>

<style scoped lang="scss">
	.head {
		width: 750rpx;
		height: 440rpx;
		background: url('../../static/background/home-banner.svg') repeat fixed center;
