		top: -90px; */
	}

	.account {
		width: 686rpx;
		height: 64rpx;
		margin: 104rpx auto 0;
	}

	.user-img {
		width: 64rpx;
		height: 64rpx;
	}

	.fund {
		width: 518rpx;
		height: 96rpx;
		margin: 64rpx auto 0;

	}

	.container {
		width: 100%;
		height: 240rpx;
		margin: 32rpx 0 48rpx 32rpx;
	}

	.myCharts {
		width: 208rpx;
		height: 146rpx;
	}

	.item {
		width: 208rpx;
		height: 240rpx;
		border-radius: 48rpx;
	}

	.title {
		margin: 32rpx 0 0 32rpx;
	}

	.number {
		margin: 8rpx 0 0 32rpx;
	}
</style>